import { useContext, useState } from 'react';
import * as S from './style';
import * as C from '..';
import TteokbokkiImg from '../../assets/svg/tteokbokki.svg';
import MalaImg from '../../assets/svg/mala.svg';
import { ClickContext } from '../../Context';

const DrawPage = ({ menuKo, menuEn }) => {
  const { setIsClicked } = useContext(ClickContext);
  /* Menu List */
  const malaMenu = [
    {
      type: 'vegetable',
      name: '',
    },
    {
      type: 'vegetable',
      name: '',
    },
    {
      type: 'vegetable',
      name: '',
    },
    {
      type: 'vegetable',
      name: '',
    },
    {
      type: 'vegetable',
      name: '',
    },
    {
      type: 'vegetable',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'nudleAndTteok',
      name: '',
    },
    {
      type: 'seafoodAndMeat',
      name: '',
    },
    {
      type: 'seafoodAndMeat',
      name: '',
    },
    {
      type: 'seafoodAndMeat',
      name: '',
    },
    {
      type: 'seafoodAndMeat',
      name: '',
    },
    {
      type: 'mushroom',
      name: '',
    },
    {
      type: 'mushroom',
      name: '',
    },
    {
      type: 'mushroom',
      name: '',
    },
    {
      type: 'mushroom',
      name: '',
    },
    {
      type: 'mushroom',
      name: '',
    },
    {
      type: 'tofu',
      name: '',
    },
    {
      type: 'tofu',
      name: '',
    },
    {
      type: 'tofu',
      name: '',
    },
    {
      type: 'tofu',
      name: '',
    },
  ];

  /* Malatang State */
  const [vegetable, setVegetable] = useState('');
  const [nudleAndTteok, setNudleAndTteok] = useState('');
  const [seafoodAndMeat, setSeafoodAndMeat] = useState('');
  const [mushroom, setMushroom] = useState('');
  const [tofu, setTofu] = useState('');

  const [tteokbokki, setTteokbokki] = useState('');

  const [clickButton, setClickButton] = useState(false);
  const sortObj = type => {
    let sort = malaMenu
      .filter(x => x.type === type)
      .sort(() => Math.random() - 0.5);

    if (type === 'vegetable' || type === 'nudleAndTteok') {
      sort = `${sort[0].name}, ${sort[1].name}`;
    } else {
      sort = `${sort[0].name}`;
    }

    return sort;
  };

  const handleClickButton = menu => {
    if (menu === 'malatang') {
      setVegetable(sortObj('vegetable'));
      setNudleAndTteok(sortObj('nudleAndTteok'));
      setSeafoodAndMeat(sortObj('seafoodAndMeat'));
      setMushroom(sortObj('mushroom'));
      setTofu(sortObj('tofu'));
    } else if (menu === 'tteokbokki') {
      setTteokbokki('  !');
    }
    setClickButton(true);
    setIsClicked(prev => !prev);
  };

  return (
    <S.BacgroundStyle>
      <C.Title description={`${menuKo}   !`} />
      <S.DivStyle>
        <S.TopingBox>
          <S.MenuBox>
            {clickButton === false ? null : menuEn === 'malatang' ? (
              <C.DrawMotion
                img={MalaImg}
                menu={menuKo}
                vegetable={vegetable}
                nudleAndTteok={nudleAndTteok}
                seafoodAndMeat={seafoodAndMeat}
                mushroom={mushroom}
                tofu={tofu}
              />
            ) : (
              <C.DrawMotion
                img={TteokbokkiImg}
                menu={menuKo}
                tteokbokki={tteokbokki}
              />
            )}
          </S.MenuBox>
        </S.TopingBox>
        <C.Button
          onClick={() => {
            handleClickButton(menuEn);
          }}
        />
      </S.DivStyle>
    </S.BacgroundStyle>
  );
};

export default DrawPage;
